import React, { useEffect, useState } from 'react';
import { BasicPubSubClient } from '@twurple/pubsub';
import { ChatClient } from '@twurple/chat';

const Page = () => {
  const [currentSubCount, setCurrentSubCount] = useState(0);
  const [moderationAction, setModerationAction] = useState(0);
  const [moderationActionNeed, setModerationActionNeed] = useState(0);
  const [moderationActionTotal, setModerationActionTotal] = useState(0);

  useEffect(() => {
    document.title = 'LsCo Software - Modding Marathon';
    document.body.style.backgroundColor = 'initial !important';

    const fetchData = async () => {
      const authToken = new URLSearchParams(window.location.search).get('access_token');
      const username = new URLSearchParams(window.location.search).get('login');
      const userID = new URLSearchParams(window.location.search).get('id');
      const channels = await getChannels(username, userID);

      const topics = channels.map((i) => `chat_moderator_actions.${userID}.${i}`);

      const pubsub = new BasicPubSubClient();

      pubsub.onDisconnect((isError, reason) => {
        console.log('[PubSub] Disconnected', reason);
      });

      pubsub.onListenError((topic, error) => {
        console.error(`[PubSub] An error has occurred on topic '${topic}':`, error);
      });

      pubsub.onConnect(async () => {
        console.log('[PubSub] Connected');
        await pubsub.listen(topics, authToken);
      });

      pubsub.onMessage(async (topic, message) => {
        if (topic.split('.').shift() === 'chat_moderator_actions') {
          if (message.data.created_by_user_id === userID) {
            switch (message.data.moderation_action) {
              case 'delete':
              case 'timeout':
              case 'ban': {
                setModerationAction((prevAction) => prevAction + 1);
                setModerationActionTotal((prevTotal) => prevTotal + 1);
                break;
              }
              default:
                break;
            }
          }
        }
      });

      const client = new ChatClient({ channels: [username] });

      client.onMessage((channel, user, message, userstate) => {
        if (userstate.userInfo.userId === userID) {
          if (!message.startsWith('!')) return;
          const msg = message.slice('!'.length).trim().split(/ +/);

          console.log(msg);
          if (msg[0]?.toLowerCase() === 'overlay') {
            switch (msg[1]?.toLowerCase()) {
              case 'reset': {
                localStorage.setItem('marathon_subs', 0);
                localStorage.setItem('marathon_moderation_action_need', 0);
                localStorage.setItem('marathon_moderation_action', 0);
                setModerationAction(0);
                setModerationActionNeed(0);
                setCurrentSubCount(0);
                return;
              }

              case 'subs': {
                if (!msg[2]) return;

                if (!/^[0-9]+$/.test(msg[2])) return;

                setCurrentSubCount(Number(msg[2]));
                localStorage.setItem('marathon_subs', Number(msg[2]));
                return;
              }

              case 'mod_actions': {
                if (!msg[2]) return;

                if (!/^[0-9]+$/.test(msg[2])) return;

                setModerationAction(Number(msg[2]));
                localStorage.setItem('marathon_moderation_action', Number(msg[2]));
                return;
              }

              case 'mod_actions_need': {
                if (!msg[2]) return;

                if (!/^[0-9]+$/.test(msg[2])) return;

                setModerationActionNeed(Number(msg[2]));
                localStorage.setItem('marathon_moderation_action_need', Number(msg[2]));
                return;
              }

              case 'mod_actions_total': {
                if (!msg[2]) return;

                if (!/^[0-9]+$/.test(msg[2])) return;

                setModerationActionTotal(Number(msg[2]));
                localStorage.setItem('marathon_moderation_action_total', Number(msg[2]));
                return;
              }

              default:
                return;
            }
          }
        }
      });

      client.onSub(() => {
        setCurrentSubCount((prevCount) => prevCount + 1);
        setModerationActionNeed((prevActionNeed) => prevActionNeed + (URLParams.get('countup') || 5));
      });

      client.onResub(() => {
        setCurrentSubCount((prevCount) => prevCount + 1);
        setModerationActionNeed((prevActionNeed) => prevActionNeed + (URLParams.get('countup') || 5));
      });

      client.onSubGift(() => {
        setCurrentSubCount((prevCount) => prevCount + 1);
        setModerationActionNeed((prevActionNeed) => prevActionNeed + (URLParams.get('countup') || 5));
      });

      pubsub.connect();
      client.connect();

      return () => {
        pubsub.disconnect();
        client.disconnect();
      };
    };

    fetchData();
  }, []);

  return (
    <div
      style={{
        fontFamily: 'Comic Neue',
        fontSize: 'xx-large',
        textAlign: 'end',
        verticalAlign: 'middle',
        textShadow: '4px 4px 6px rgba(0, 0, 0, 0.4)',
        margin: '2.5rem'
      }}
    >
      Subs: {format(currentSubCount)} - Mod Actions: {format(moderationAction)}/{format(Number(moderationActionNeed))} - Total:{' '}
      {format(moderationActionTotal)}
    </div>
  );
};

const format = (number) => {
  return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.');
};

const getChannels = async (login, id) => {
  const request = await fetch('https://api.modscanner.com/twitch/user/' + login);

  if (!request.ok) return [];

  const data = await request.json();

  return [...data.moderating.map((i) => i.id), id];
};

export default Page;
